
/// <reference path="viewmodels.generated.d.ts" />


module MMDash {
    export class StreamModule {
        public calls = new ListViewModels.CallSignList();

        load() {
            this.calls.load();
        }

        constructor() {
            var self = this;
        }

    }

    $(function () {
        var vm = new StreamModule();
        ko.applyBindings(vm);
        vm.load();
    })

}